.incomplete-area {
    border: 2px solid #aacfd0;
    width: 400px;
    min-height: 200px;
    padding: 8px;
    margin: 8px;
    border-radius: 8px;
}

.complete-area {
    border: 2px solid #aacfd0;
    width: 400px;
    min-height: 200px;
    padding: 8px;
    margin: 8px;
    border-radius: 8px;  
    background-color: #c6e5d9;
}

.title {
    text-align: center;
    margin-top: 0;
    font-weight: bold;
}

.list-row {
    display: flex;
    align-items: center;
}

.todo-item {
    margin: 6px;
}

